body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.search-form {
  text-align: center;
  margin-top: 20px;
}

.search-form input {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px;
  display: flex;
}

.photo-card {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  margin: 10px;
  transition: transform .3s ease-in-out;
  overflow: hidden;
  box-shadow: 0 4px 8px #0000001a;
}

.photo-card img {
  border-radius: 8px 8px 0 0;
  max-width: 100%;
  height: auto;
}

.info-item {
  color: #555;
  margin: 10px 0;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

.load-more:hover {
  background-color: #27ae60;
}

.photo-card img {
  max-width: 100%;
  height: auto;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.photo-card {
  flex: 1 0 calc(25% - 10px);
}

.info {
  padding: 10px;
}

